<!--视屏列表-->
<template>
  <div class="home">
    <!-- 顶部选择框-->
    <div class="homeHeader">
      <div class="HeaderLeft">
        <el-button type="primary" @click="getAllVideoList(1)">获取视屏列表</el-button>
      </div>
    </div>
    <!--中间部分-->
    <el-table :data="tableData" style="width: 100%">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="videoId" label="视屏ID" width="100"></el-table-column>
      <el-table-column prop="name" label="视屏名称" width="650"></el-table-column>
      <el-table-column prop="nickname" label="上传作者"></el-table-column>
      <el-table-column prop="time" label="上传时间"></el-table-column>

      <el-table-column label="操作" width="150">
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini" circle @click="editRow(scope.row)"></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" circle @click="deleteTable(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--分页表单-->
    <!--分页表单-->
    <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize"
      @current-change="handleCurrentChange" class="pageBody">
    </el-pagination>

  </div>
</template>

<script>
import { getVideoList,deleteVideoAction } from '../../services/videoManage'
export default {
  name: 'videoManage',
  data() {
    return {
      tableData: [],
      total: 0, //页面总条数
      page: 1, //请求第一次的params
      pageSize: 0,
      listType: 0,   //0表示的是查看全部的视屏列表
      statusType:0   //获取审核视屏的状态
    }
  },
  mounted() {
    this.getAllVideoList(1)
  },
  methods: {
    //获取所有的视屏列表
    getAllVideoList(pageNum) {
      getVideoList(pageNum).then((res) => {
        console.log(res);
        this.page = 1
        this.tableData = res.data.data.list
        this.total = res.data.data.total; //后端返回的总数量
        this.pageSize = res.data.data.pageSize //后端返回的每一页的总数
      })
    },
    //分页
    handleCurrentChange(page) {
      this.page = page;
      console.log(this.listType);
      if (this.listType === 0) {
        this.getAllVideoList(this.page);
      } else {
        //把审核视屏的状态发送给请求中 
        this.getAuditVideoList(this.statusType)
      }
    },


    // 删除
    deleteTable(row) {
      deleteVideoAction(row.videoId).then((res)=>{
        if(res.data.code === 200){
          this.$message.success('删除视屏成功!!')
          this.getAllVideoList(1)
        }
      })
    },
    // 编辑表格
    editRow(row) {
      this.$refs.ConstitutionAAssess.show(row)
    },

  }

}
</script>

<style scoped lang="less">
.homeHeader {
  margin: 15px 10px 5px 10px;
  display: flex;
  justify-content: space-between;
  .HeaderLeft{
    margin-left: 10px;
  }
  .HeaderRight{
    margin-right: 10px;
  }
}

.pageBody {
  margin-top: 15px;
  display: flex;
  justify-content: center;
  align-content: center;
}
</style>
